.toolbar{
    position: fixed;
    top:0;
    left:0;
    width :100%;
    background: #521751;
    height:56px;
}

.toolbar_navigation{
    display: flex;
    height: 100%;
    padding:0 1rem;
    align-items: center;
}


.toolbar_logo a{
color:white;
text-decoration: none;
font-size: 1.5rem;

}

.toolbar_logo{
margin-left: 1rem;
}

.toolbar_navigation-items a{
    color: white;
    text-decoration: none;
}
.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active{
    color: #fa923f;
}


.toolbar_navigation-items ul{
    list-style: none;
    margin:0;
    padding:0;
    display: flex;
}
.toolbar_navigation-items li{
    padding: 0 0.5rem;
}

.spacer{
    flex:1;
}




